@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
.switch-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin:20px 20px 10px 20px;
}
.switch-container h1 {
  margin: 10px;
}
.switch-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}
.switch-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 60px;
  height: 30px;
  background: grey;
  border-radius: 100px;
  position: relative;
  transition: background-color .2s;
}

.switch-label .switch-button {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 25px;
  height: 25px;
  border-radius: 25px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}
.switch-checkbox:checked + .switch-label .switch-button {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}
.switch-label:active .switch-button {
  width: 60px;
}

h2 {
  margin: 10px;
  vertical-align: middle;
}

.both-times {
  font-family: "Poppins", sans-serif;
  font-size: 50px;
}
