/* page enter keyframes */
@-webkit-keyframes moveInTop {
  from { 
    -webkit-transform: translate3d(0, -100%, 0); 
    visibility: visible; 
  }
  to { -webkit-transform: translate3d(0, 0, 0); }
}

@keyframes moveInTop {
  from { 
    -webkit-transform: translate3d(0, -100%, 0); 
            transform: translate3d(0, -100%, 0); 
    visibility: visible; 
  }
  to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

/* page exit keyframes */
@-webkit-keyframes moveOutTop {
  from { -webkit-transform: translate3d(0, 0, 0); }
  to { -webkit-transform: translate3d(0, -100%, 0); visibility: hidden; }
}

@keyframes moveOutTop {
  from { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
  to { -webkit-transform: translate3d(0, -100%, 0); transform: translate3d(0, -100%, 0); visibility: hidden; }
}

.home-page.page-enter {
  -webkit-animation: moveInTop 0.5s forwards;
  animation: moveInTop 0.5s forwards;
}

.home-page.page-exit {
  -webkit-animation: moveOutTop 0.5s forwards;
  animation: moveOutTop 0.5s forwards;
}

