}

.tooltip li img {
  width: 30px;
  margin: 5px;
  vertical-align: middle;
}

.tooltip .bottom {
  min-width:300px;
  top:60px;
  left:50%;
  -webkit-transform:translate(-50%, 0);
          transform:translate(-50%, 0);
  padding:20px;
  color: black;
  background-color:#EEEEEE;
  border-radius:8px;
  position:absolute;
  z-index:99999999;
  box-sizing:border-box;
  box-shadow:0 1px 8px rgba(0,0,0,0.5);
  display:none;
}

.tooltip:hover .bottom {
  display:block;
}

.tooltip .bottom i {
  position:absolute;
  bottom:100%;
  left:50%;
  margin-left:-12px;
  width:24px;
  height:12px;
  overflow:hidden;
}

.tooltip .bottom i::after {
  content:'';
  position:absolute;
  width:12px;
  height:12px;
  left:50%;
  -webkit-transform:translate(-50%,50%) rotate(45deg);
          transform:translate(-50%,50%) rotate(45deg);
  background-color:#EEEEEE;
  box-shadow:0 1px 8px rgba(0,0,0,0.5);
}

